body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  @include light;
  color: $c-text;
  @include no-scroll; }

input, textarea {
  font-family: inherit;
  @include placeholder {
    font-family: inherit; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.logo {
  color: $white;
  @include extrabold;
  text-transform: uppercase;
  font-size: $fz-md;
  // max-width: 150px
  @include flex(flex-start, center);
  line-height: 0.95;

  &__img {
    margin-right: 12px; }
  &__text {
    span {
      display: block; } } }

.link-arrow {
  color: $white;
  position: relative;
  @include inline-flex(flex-start, center);
  @include hover {
    .link-arrow__arrow {
      width: 70px; } }
  &__title {
    margin-right: 20px; }
  &__arrow {
    position: relative;
    width: 50px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: inline-block;
    transition: transform .3s, width .3s;
    &::before {
      width: 100%;
      content: '';
      position: absolute;
      height: 1px;
      background-color: currentColor;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%);
      transition: width .3s; }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, currentColor, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg);
      right: 0; } } }

.text-stroke {
  font-size: 250px;
  text-transform: uppercase;
  @include bold;
  -webkit-text-stroke: 1px $gray-dark;
  color: transparent; }

.flashIn {
